* {
  box-sizing: border-box;
}

body,
html {
  margin: 0;
  font-family: 'Quattrocento', serif;
  height: 100%;
  overflow: hidden;
}

main,
article {
  height: 100%;
}

body {
  max-height: 100%;
  overflow: hidden;
}

.gallery-image {
  display: block;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

h2 {
  margin: 0;
}

.popup {
  position: fixed;
  top: 50%;
  left: 50%;
  background: #fff;
  width: 300px;
  height: 300px;
  margin: -150px auto auto -150px;
  z-index: 1;
  border: none;
  padding: 20px;
  text-align: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.close-button {
  position: absolute;
  top: 20px;
  right: 20px;
  width: 40px;
  height: 40px;
  border: 2px solid #000;
  border-radius: 20px;
  background: #fff;
  overflow: hidden;
  padding: 0;
  line-height: 0;
  &:before {
    content: 'x';
    margin: 0 40px 0 12px;
    font-size: 30px;
  }
}
